<template>
  <div id="i_perfect_infor">
    <nav>
      <van-icon name="arrow-left" @click="$global.previous()" />
    </nav>
    <main>
      <article>
        <!-- <header>{{title}}</header> -->
        <ul>
          <li>
            <p class="row1"></p>
            <p class="row2">
            <van-dropdown-menu>
                <van-dropdown-item  v-model="form.investorsType" :options="option1" />
              </van-dropdown-menu>
<!--              <van-field  v-model="form.investorsType" placeholder="-" />-->
            </p>
          </li>
          <li v-show="form.investorsType==2">
            <p class="row1"></p>
            <p class="row2">
              <van-field  v-model="form.investorsCompany" placeholder="-" clearable />
            </p>
          </li>
          <li>
            <p class="row1"></p>
            <p class="row2">
              <van-dropdown-menu>
                <van-dropdown-item @close="choose_nation" placeholder="-" v-model="form.investorsArea"  :options="countrylist" />
              </van-dropdown-menu>
<!--              <van-field v-model="form.investorsArea" placeholder="-" clearable />-->
            </p>
          </li>

          <li>
            <p class="row1"></p>
            <p class="row2">
              <van-field  v-model="form.investorsName" placeholder="" clearable />
            </p>
          </li>
          <li>
            <p class="row1"></p>
            <p class="row2">
              <van-field v-model="form.investorsMobile" placeholder="" clearable />
            </p>
          </li>
          <li>
            <p class="row1"></p>
            <p class="row2">
              <van-field v-model="form.investorsEmail" placeholder="" clearable />
            </p>
          </li>
          <li >
            <p class="row1"></p>
            <p class="row2">
              <van-field v-model="form.investorsCompanyAddress" placeholder="" clearable />
            </p>
          </li>
          <li>
            <p class="row1"></p>
            <p class="row2">
              <van-checkbox-group v-model="form.interestedIndustries">
                <van-checkbox
                  v-for="(item) in industrylist"
                  :key="item.industryId"
                  :name="item.industryNameCh"
                >{{item.industryNameCh}}</van-checkbox>
              </van-checkbox-group>
            </p>
          </li>
        </ul>
        <footer>
          <aside>
            <button @click="submit"></button>
          </aside>
        </footer>
      </article>
    </main>
    <mbottom></mbottom>
  </div>
</template>
<script>
export default {
  name: "i_perfect_infor",
  data() {
    return {
      title: "",
      form: {
        signId: this.$route.query.signId,
        signStatus: 8,
        investorsId: this.$route.query.investorsId,
        investorsType: null,
        investorsCompany: "",
        investorsName: "",
        investorsArea: "",
        investorsEmail: "",
        investorsMobile: "",
        interestedIndustries: [],
        investorsCompanyAddress: "",
        investorsName: ""
      },
      industrylist: [],
      countrylist:[],
      option1: [{ text: "", value: 1 }, { text: "", value: 2 }]
    };
  },
  // beforeRouteEnter(to,from,next){
  //   next(vm=>{
  //     // vm.targetU
  //     // console.log(vm);
  //     vm.target=JSON.parse(to.query.investor_infor)
  //     console.log(vm.target);

  //   })
  // },
  created() {
    this.$axios({
      method: "get",
      url: `${this.$baseurl}/bsl_web/base/countryList.do?searchKey=`
    })
      .then(res => {
        console.log(res)
        if(res.data.resultCode==10000){
          // this.title = res.data.data.projectName;
          // this.countrylist = res.data.data;
          this.countrylist = res.data.data;
          for (let i = 0; i < this.countrylist.length; i++) {
            this.countrylist[i].value = this.countrylist[i].countryTcname;
            this.countrylist[i].text = this.countrylist[i].countryTcname;
          }
        }

      })
      .catch(err => {
        console.log(err);
      });
    this.$axios({
      method: "get",
      url: `${this.$baseurl}/bsl_web/base/getAllIndustry`
    })
      .then(res => {
        if(res.data.resultCode==10000){
          this.title = res.data.data.projectName;
          this.industrylist = res.data.data;
        }

      })
      .catch(err => {
        console.log(err);
      });
  },
  methods: {
    choose_nation(){
      // if(!this.form.investorsArea && this.form.investorsArea!==0){
      //   this.form_err.investorsArea=""
      // }else{
      //   this.form_err.investorsArea=''
      // }
    },
    submit() {
      let formtable = JSON.parse(JSON.stringify(this.form));
      let interestedIndustries = this.form.interestedIndustries.join("/");
      formtable.interestedIndustries = interestedIndustries;
      // let  formtable= {
      //   signId: this.$route.query.signId,
      //   signStatus: 6,
      //   investorsId: this.$route.query.investorsId,
      //   investorsType: 1,
      //   investorsCompany: "2",
      //   investorsName: "2",
      //   investorsArea: "2",
      //   investorsEmail: "2",
      //   investorsMobile: "2",
      //   interestedIndustries: a ,
      //   investorsCompanyAddress: "2",
      //   investorsName: "33"
      // };
      if (formtable.investorsType == "") {
        formtable.investorsType = 1;
      } else if (formtable.investorsType == "") {
        formtable.investorsType = 2;
      }

      console.log(formtable);
      this.$axios({
        method: "post",
        url: `${this.$baseurl}/bsl_web/projectSign/signProject3`,
        data: this.$qs.stringify(formtable)
      })
        .then(res => {
          console.log(res);
          // if (res.data.resultCode == 10000) {
          //   this.dialog("", res.data.resultCode);
          //   // this.$routerto("mhome");
          // }
          this.dialog(res.data.resultDesc, res.data.resultCode);
          // else if (res.data.resultDesc == 10017) {
          //   this.dialog("");
          // } else if (res.data.resultCode == 10010) {
          //   this.dialog("");
          // } else if (res.data.resultCode == 10011) {
          //   this.dialog("");
          // } else if (res.data.resultCode == 10012) {
          //   this.dialog("");
          // } else if (res.data.resultCode == 10013) {
          //   this.dialog("");
          // }else if (res.data.resultCode == 10051) {
          //   this.dialog("");
          // }
        })
        .catch(err => {
          console.log(err);
        });
    },
    dialog(str, num) {
      this.$dialog
        .confirm({
          title: str
        })
        .then(() => {
          if (num == 10000) {
            this.$routerto("mhome");
          }
        }).catch(err=>{

      })
    }
  }
};
</script>
<style lang="scss">
#i_perfect_infor {
  .van-cell {
    font-size: 0.38rem;
    padding: 0 1rem;
    line-height: 1rem;
    // padding: 0;
  }
  .van-dropdown-menu__title {
    font-size: 0.38rem;
    width: 100%;
    // text-align: left;
  }
  .van-icon-arrow-left {
    position: absolute;
    left: 0.6rem;
    top: 50%;
    -webkit-transform: translate(0, -50%);
    transform: translate(0, -50%);
  }
  .van-dropdown-menu {
    height: 1rem;
    border-radius: 0.05rem;
    box-sizing: border-box;
    border: 1px solid #ababab;
    background: #f6f6f6;
  }
  .van-field__body {
    //  width: 100%;
    height: 1rem;
    line-height: 1rem;
     border: 1px solid #ababab;
    border-radius: 0.05rem;
    background: #f6f6f6;
    padding: 0 0.2rem;
    box-sizing: border-box;
  }

  .van-icon-success {
    height: 0.4rem;
    width: 0.4rem;
    line-height: 0.4rem;
    font-size: 0.3rem;
  }
  .van-field__control {
    // padding: 0 0.2rem;
    // height: 0.5rem;
  }
  .van-field__clear {
    font-size: 0.3rem;
  }
  .van-field {
    padding: 0;
  }
  .van-cell--clickable {
    .van-cell__title {
      margin-left: 2.7rem;
    }
    .van-cell__value {
      display: none;
    }
  }
  .van-checkbox__icon {
    height: 100%;
  }

  .van-dropdown-menu__title::after {
    border: 0.1rem solid;
    /*top: 50%;*/
    right: 0.5rem;
    margin: 0;
    transform: rotate(0);
    border-color: currentColor transparent transparent transparent;
  }

  .van-dropdown-menu__title--down::after {
    border: 0.1rem solid;
    /*top: 50%;*/
    border-color: currentColor transparent transparent transparent;
  }
}
</style>
<style lang="scss" scoped>
#i_perfect_infor {
  width: 100%;
  nav {
    width: 100%;
    text-align: center;
    line-height: 1.5rem;
    z-index: 5;
    height: 1.5rem;
    position: fixed;
    top: 0;
    font-size: 0.46rem;
    background: white;
    border-bottom: 0.1rem solid #b5b5b5;
  }
  main {
    margin-top: 1.5rem;
    background: #ffffff;
    aside {
      display: flex;
      width: 100%;
      height: 3rem;
      justify-content: center;
    }
    article {
      margin: 0 0 1rem 0;
      header {
        // border-bottom: 0.1rem solid #b5b5b5;
        height: 2rem;
        font-size: 0.38rem;
        color: #0f6ebe;
        text-align: center;
        // font-weight: 600;
        line-height: 2rem;
      }
      ul {
        padding: 0.5rem;
        li {
          margin-bottom: 0.4rem;
          display: flex;
          /*line-height: 0.48rem;*/
          align-items: center;
          font-size: 0.3rem;
          .row1 {
            color: #4c4c4c;
            font-weight: 600;
            width: 3rem;
          }
          .row2 {
            width: 7rem;
            word-break: break-all;
            color: #787878;
          }
        }
      }
      footer {
        padding: 0 0.5rem 0.5rem 0.5rem;
        button {
          width: 8rem;
          height: 1rem;
          background: #00adef;
          color: white;
        }
      }
    }
  }
}
</style>



